
import axios from 'axios';
import qs from 'qs';
import { IonButton, IonLabel, IonItem, IonRow, IonCol, IonText, IonContent, IonList, IonInput } from '@ionic/vue';
import { defineComponent } from 'vue';

if(localStorage.getItem("token")){ 
   //  location.href = '/home'; 
}

export default defineComponent({
  data(){
    return {
      username: "",
      password: "",
      submitted: false, 
      showUsernameError: false,
      showPasswordError: false,
      usernameError: "",
      passwordError: "",
      continueWithGoogleScreen: false,
      hasError: false,
      errorMessage: ""
    }
  },
  components: {
    IonButton,
    IonLabel, 
    IonItem, 
    IonRow,
    IonCol, 
    IonText, 
    IonContent,
    IonList,
    IonInput
  },
  methods: {
    async onLogin() {
      if(this.username == ""){
        this.showUsernameError = true;
        this.usernameError = "Field Username mustn't be empty.";
      }else if(this.username.length < 3 && this.username != ""){
        this.showUsernameError = true;
        this.usernameError = "Username to short";
      }else{
        const options = {
          method: 'POST',
          data: qs.stringify({email: this.username, password:this.password}), 
          url: 'https://alex.polan.sk/control-center/login.php',
        };
        await axios(options)
          .then(
            res => {
              console.log(res.data);
              if(res.data.token){
                localStorage.setItem("token", res.data.token)
                console.log(localStorage.getItem("token"));
                location.href = '/'; 
              }else if(res.data.errorMessage){
                this.hasError = true
                this.errorMessage = res.data.errorMessage
                this.showUsernameError = true;
                this.usernameError = "Username or Password Incorrect";
                this.showPasswordError = true;
                this.passwordError = "Username or Password Incorrect";
              }else if(res.data.command){
                if(res.data.command == "verify-ip"){
                  localStorage.setItem("verification_email", res.data.verification_email)
                  localStorage.setItem("verification_name", res.data.verification_name)
                  localStorage.setItem("verification_token", res.data.verification_token)
                  location.href = '/login/verification/'; 
                }
                this.hasError = true
                this.errorMessage = "send"
              }
            },
            err => {
              console.log(err);
              this.hasError = true;
              this.errorMessage = "Cannot connect to server";
            }
          );
      }
    },
  }
})

